/* Breakpoints */
@import url(../assets/fonts/Museo100-Regular.css);
@import url(../assets/fonts/Museo300-Regular.css);
@import url(../assets/fonts/Museo500-Regular.css);
@import url(../assets/fonts/Museo700-Regular.css);
@import url(../assets/fonts/Museo900-Regular.css);
input {
  font-family: "Museo500-Regular", Helvetica, Arial, sans-serif;
}
h1 {
  line-height: 60px;
}
h2 {
  font-size: 32px;
  line-height: 40px;
}
#app {
  font-family: "Museo500-Regular", Helvetica, Arial, sans-serif !important;
  font-size: 16px;
  line-height: 20px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: left;
}
#app .home {
    width: 100vw;
    height: 100vh;
}
#app .home main {
      height: 90vh;
      background: url("../assets/bg@2x.png");
      background-size: cover;
}
#app .home main .main {
        position: absolute;
        left: 50%;
        position: absolute;
        top: 50%;
        -webkit-transform: translateX(-50%) translateY(-50%);
                transform: translateX(-50%) translateY(-50%);
        background: #fff;
        color: #000;
        box-shadow: 0px 10px 15px #888888;
}
#app .home main .main.questionnaire {
          width: 80vw;
          height: 70vh;
          text-align: center;
          transition: all 1s ease-out;
}
#app .home main .main.questionnaire img {
            width: 40%;
            height: auto;
            margin: 0 5%;
}
#app .home main .main.product {
          z-index: 96;
          width: 90vw;
          height: 70vh;
}
#app .home main .main.product .left {
            float: left;
            width: 40%;
}
#app .home main .main.product .left img {
              width: 100%;
              padding: 3%;
}
#app .home main .main.product .left h2 {
              position: absolute;
              bottom: 3vh;
              left: 20%;
              height: auto;
              color: #74a83e;
              text-shadow: 0px 3px 10px white;
}
#app .home main .main.product .right {
            float: left;
            width: 60%;
}
#app .home main .main.product .right .description {
              width: 80%;
              display: inline;
}
#app .home main .main.product .right .description:after {
                content: '\a\a';
                white-space: pre;
}
#app .home main .main.product .right .stars {
              color: #ffbf00;
}
#app .home main .main.product .right .stars:after {
                content: '\a';
                white-space: pre;
}
#app .home main .main.product .right .stars .filled {
                color: #494949;
}
#app .home main .main.product #buy {
            color: yellow;
}
#app .home main #recommended {
        box-shadow: 0px 10px 15px #888888;
        position: absolute;
        right: 3vw;
        top: 5vw;
        z-index: 99;
        width: 20vh;
        height: 20vh;
        color: #fff;
        border-radius: 10vh;
        background: #42fce6;
        text-align: center;
        display: inline-block;
        vertical-align: middle;
        -webkit-transform: rotate(7deg);
                transform: rotate(7deg);
}
#app .home main #recommended p {
          display: inline-block;
          height: 100%;
}
#app .home main .popup {
        position: absolute;
        padding: 10px;
        color: white;
        border-radius: 3px;
        cursor: pointer;
        transition: all .3s ease;
}
#app .home main .popup.warning {
          left: 2vh;
          bottom: 5vh;
          background: rgba(249, 60, 10, 0.79);
          font-size: 24px;
          line-height: 30px;
          font-weight: 900;
}
#app .home main .popup.invite {
          right: 2vh;
          bottom: 25vh;
          background: rgba(10, 213, 249, 0.79);
          font-weight: 300;
          font-size: 24px;
          line-height: 30px;
}
#app .home main .popup.freebie {
          left: 2vh;
          top: calc(10vh + 44px);
          background: rgba(10, 249, 55, 0.79);
          font-weight: 700;
          font-size: 32px;
          line-height: 40px;
}
#app .home main .video {
        position: absolute;
        width: 65vw;
        background: #fff;
        color: #fff;
        text-align: center;
        transition: all 1s ease-out;
}
#app .home main .video .container {
          background: #fff;
}
#app .home main .video.popup {
          width: 30vw;
          top: 2vh;
          right: 10vh !important;
}
#app .home #alert {
      position: fixed;
      z-index: 10;
      background: rgba(255, 85, 85, 0.5);
      color: #000;
      font-size: 32px;
      line-height: 40px;
      width: 100vw;
      height: 100vh;
      transition: all .5s ease-out;
      pointer-events: none;
      -webkit-text-stroke-width: 1px;
      -webkit-text-stroke-color: white;
      box-shadow: inset 0 0 100px #9a0000;
      display: block;
      text-align: center;
}
#app .home #alert p {
        display: inline;
        padding: 10px 30px;
        border-radius: 5px;
        background: rgba(249, 60, 10, 0.79);
        font-weight: 900;
        box-shadow: 0px 0px 30px rgba(249, 60, 10, 0.79);
}
#app .stop-scroll {
    height: 100%;
    overflow: hidden;
}
@keyframes pulse {
  from-background-color: rgba(241, 67, 67, 0.3);
  to-background-color: #f14343;
}
@-webkit-keyframes pulse {
  from-background-color: rgba(241, 67, 67, 0.3);
  to-background-color: #f14343;
}
#app .inline-helper {
    display: inline-block;
    height: 100%;
    vertical-align: middle;
}
#app .home footer {
  height: auto;
  background: #141414;
  color: #fff;
  line-height: 24px;
  padding-bottom: 32px;
}
#app .home footer a {
    color: #fff;
    text-decoration: none;
}
#app .home footer a:visited {
      color: #fff;
}
#app .home footer a:hover {
      color: #fff;
      text-decoration: underline;
}
#app .home footer a:active {
      color: #fff;
}
#app .home footer h2 {
    font-weight: 700;
    text-transform: uppercase;
    opacity: 0.2;
}
#app .home footer li {
    opacity: 0.6;
}
#app .home footer .box {
    width: auto;
    margin: 0 32px;
}
#app .home footer .box .row {
      width: auto;
      min-width: 22%;
      display: inline-block;
      vertical-align: top;
      margin-top: 20px;
}
#app .home h1 {
  font-size: 48px;
  line-height: 60px;
}
.home header {
  background: #141414;
  height: 10vh;
}
.home header img {
    width: auto;
    height: 80%;
    margin: 10px;
}
.splash header {
  position: absolute;
  z-index: 10;
  height: 6vh;
  width: 100vw;
}
header .user {
  position: absolute;
  width: 10vh;
  height: 10vh;
  right: 0;
  top: 0;
  color: white;
  text-align: center;
}
header .user .counter {
    width: 6vh;
    height: 6vh;
    border: 1px solid white;
    border-radius: 10vh;
    background: #000;
    position: absolute;
    top: 2vh;
    right: 2vh;
    text-align: center;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
header .user .counter p {
      display: inline-block;
      line-height: 6vh;
}
.splash {
  display: block;
  height: 100vh;
}
.splash .video.main {
    width: 70vw;
    box-shadow: 0px 10px 15px #888888;
    left: 50%;
    position: absolute;
    top: 50%;
    -webkit-transform: translateX(-50%) translateY(-50%);
            transform: translateX(-50%) translateY(-50%);
}
.splash a {
    font-family: "Museo500-Regular", Helvetica, Arial, sans-serif;
    text-decoration: none;
    color: #000;
}
.splash a:active {
      color: #000;
}
.splash a:hover {
      color: #000;
      text-decoration: underline;
}
.splash a:visited {
      color: #000;
}
.splash .menu {
    width: 100%;
    height: 6vh;
    text-align: right;
    clear: both;
}
.splash .menu #logo {
      float: left;
      display: inline-block;
      height: 100%;
      width: auto;
      vertical-align: top;
      text-align: left !important;
}
.splash .menu #links {
      float: right;
      width: auto;
      display: inline-block;
      vertical-align: top;
      margin-top: 10px;
}
@media (max-width: 800px) {
.splash .menu #links {
          float: left;
          padding-left: 10px;
}
}
.splash .menu #links > a {
        padding: 0 10px 0 0;
}
#container {
  position: absolute;
  width: 100%;
  padding: 0 3%;
  bottom: 2vh;
}
#container #logo {
    width: 100%;
}
#container #message {
    font-family: "Museo300-Regular", Helvetica, Arial, sans-serif;
    text-align: center;
    font-size: 24px;
    line-height: 34px;
}
@media (max-width: 800px) {
#container #message {
        font-size: 16px;
        line-height: 28px;
}
}



